import { Layer } from 'react-konva';
import * as React from 'react';

import { BoardField, FieldState, Point } from '../../utils/objectTypes';
import { layouter } from '../../utils/functions';
import BoardFieldComponent from './GameElements/FieldVisual';
import { Component } from 'react';

export interface Props {
  fields: FieldState
}

class Fields extends Component<Props, JSX.Element[]>{
  private canvasDimension: Point;
  private fieldComponents: JSX.Element[];
  
  constructor(props: Props = {fields:{fields:[[]], dimension:{X:0, Y:0}}}){
    super(props);
    //temporarily hardcoded field size in pixels
    this.canvasDimension = {X:this.props.fields.dimension.X*15,Y:this.props.fields.dimension.Y*15};
    this.fieldComponents = [];
  }

  componentWillUpdate(nextProps: Props, nextState: JSX.Element[]){
    this.fieldComponents = [];
    this.props.fields.fields.forEach((value: BoardField[], index: number) => {
    var row: BoardField[] = value;
    var rowNo: number = index;
      row.forEach((value: BoardField, index: number)=>{
      this.fieldComponents.push(<BoardFieldComponent color={value.color} rect={layouter(this.props.fields.dimension, this.canvasDimension, {X: rowNo, Y: index})} key={getFieldKey(rowNo, index, this.props.fields.dimension.Y)} /> );
      });
    });
  }

  render(){
    return (
      <Layer>
      {this.fieldComponents}
      </Layer>
    );
}
}

export default Fields;

// helpers

function getFieldKey(X: number, Y: number, d: number): string{
  return ((X+1)*d + Y+1).toString();
}


