import * as React from 'react';
import {Stage, Layer} from 'react-konva';

import { FieldState } from '../../utils/objectTypes';
import ScoreBar from './GameElements/ScoreBar';

export interface Props {
  fields: FieldState
}

function ScoreBoard({fields = {fields:[[]], dimension: {X:0, Y:0}}}: Props) {
    //temporarily hardcoded field size in pixels
    const canvasDimension = {X:300,Y:750};
    
    const scoreBars = createScoreBars(fields);
    return (
      <div className="ScoreBoard">
        <Stage width={canvasDimension.X} height={canvasDimension.Y} >
        <Layer>
          {scoreBars}
        </Layer>
      </Stage>
      </div>
    );
}

export default ScoreBoard;

// helpers

function createScoreBars(fields: FieldState){
  var bars: ScoreBar[] = [];
  //bars.push(<ScoreBar color={'blue'} rect={{X:0,Y:0,Width:100, Height:100}} />);
  return bars;
}


