import { Point, BoardField, Player } from './objectTypes';

export function getEmptyFields(dimension: Point): BoardField[][]{
    var fields: BoardField[][] = [];

    for(var x: number = 0; x < dimension.X; x++) {
        fields[x] = [];
        for(var y: number = 0; y < dimension.Y; y++) {
            fields[x][y] = {color: 'white'};
        }
    }

    return fields;
}

export function claimStartFields(players: Player[], fields: BoardField[][]){
    players.forEach((p: Player) => {
        fields[p.coords.X][p.coords.Y].color = p.color;
    });
}

export function addPoints(p1: Point, p2: Point): Point{
    return {X: p1.X + p2.X, Y: p1.Y + p2.Y};
}

export function PointsAreEqual(p1: Point, p2: Point): boolean{
    return p1.X === p2.X && p1.Y === p2.Y;    
}

export function deepCopy(obj: any) {
    if (obj === null || typeof(obj) !== 'object' || 'isActiveClone' in obj)
      return obj;

    if (obj instanceof Date)
      var temp: any = new Date(obj) as any;
    else
      var temp: any = obj.constructor();

    for (var key in obj) {
      if (Object.prototype.hasOwnProperty.call(obj, key)) {
        obj['isActiveClone'] = null;
        temp[key] = deepCopy(obj[key]);
        delete obj['isActiveClone'];
      }
    }

    return temp;
  }

export function layouter(boardDimension: Point,
    canvasDimension: Point,
    fieldLocation: Point) {
    return{
    X: canvasDimension.X/boardDimension.X*fieldLocation.X,
    Y: canvasDimension.Y/boardDimension.Y*fieldLocation.Y,
    Width: canvasDimension.X/boardDimension.X,
    Height: canvasDimension.Y/boardDimension.Y
    };
}


