import { createBoard } from '../ReduxStore/actionTypes';
import * as actions from '../ReduxStore/actionTypes';
import { Store } from 'react-redux';

import { BoardField, GameState, Player, PlayerState, Point } from '../utils/objectTypes';

export function registerGameController(store: Store<GameState>){
    
  var state: PlayerState = store.getState().playerState;
  store.dispatch(createBoard(store.getState().fieldState.dimension));
  
  state.names.forEach(name => {
    var curPlayer = state.byName[name];
    
    store.dispatch(actions.claimFields([curPlayer.coords], curPlayer.color));
    
    setTimeout(updatePlayer, curPlayer.speed, name);
  });

  setInterval(listenForInput, 33);

  function listenForInput() {
    var state: PlayerState = store.getState().playerState;
    state.names.forEach(name => {
      var curPlayer = state.byName[name];
      analyzeInput(curPlayer);
    });
  }

  function analyzeInput(player: Player){
    if (keyboardState.pressedKeys.left) {
      if (player.direction !== 'left'){
        store.dispatch(actions.modifyPlayer({...player, direction: 'left'}));
      }
    }
    if (keyboardState.pressedKeys.right) {
      if (player.direction !== 'right'){
        store.dispatch(actions.modifyPlayer({...player, direction: 'right'}));
      }
    }
    if (keyboardState.pressedKeys.up) {
      if (player.direction !== 'up'){
        store.dispatch(actions.modifyPlayer({...player, direction: 'up'}));
      }
    }
    if (keyboardState.pressedKeys.down) {
      if (player.direction !== 'down'){
        store.dispatch(actions.modifyPlayer({...player, direction: 'down'}));
      }
    }
  }

  function updatePlayer(name: string) {
    
    var playerState: Player = store.getState().playerState.byName[name];
    // if player is on enemy territory spawn tail
    var playerField: BoardField = store.getState().fieldState.fields[playerState.coords.X][playerState.coords.Y];
    if(playerField.color !== playerState.color){
      playerState.tail = playerState.tail.concat({...playerState.coords});
      store.dispatch(actions.modifyPlayer(playerState));
    }

    // move player
    movePlayer(playerState);
    
    // if he is out of bounds, return him to start
    if (outOfBoard(playerState.coords, store.getState().fieldState.dimension)){
      playerState = {...playerState, coords: {...playerState.startCoords}, tail: [] };
      store.dispatch(actions.modifyPlayer(playerState));
    }

    // if player is on his own territory, claim fields from his tail
    if(playerField.color === playerState.color){
      store.dispatch(actions.claimFields(playerState.tail, playerState.color));
      playerState.tail = [];
      store.dispatch(actions.modifyPlayer(playerState));
    }
    setTimeout(updatePlayer, playerState.speed, playerState.name);
  }
}

// Helpers

  function movePlayer(player: Player){
    switch(player.direction){
      case 'up':
        player.coords.Y -= 1;
        break;
      case 'down':
        player.coords.Y += 1;
        break;
      case 'left':
        player.coords.X -= 1;
        break;
      case 'right':
        player.coords.X += 1;
        break;
      default: break;
    }
  }
  
  function outOfBoard(coords: Point, dimension: Point): boolean {
    return coords.X < 0 || coords.Y < 0 || coords.X >= dimension.X || coords.Y >= dimension.Y;
  }

var keyboardState = {
  pressedKeys: {
    left: false,
    right: false,
    up: false,
    down: false
  }
}

var keyMap = {
  68: 'right',
  65: 'left',
  87: 'up',
  83: 'down'
}
function keydown(event: any) {
  var key = keyMap[event.keyCode]
  keyboardState.pressedKeys[key] = true
}
function keyup(event: any) {
  var key = keyMap[event.keyCode]
  keyboardState.pressedKeys[key] = false
}

window.addEventListener("keydown", keydown, false);
window.addEventListener("keyup", keyup, false);



