import * as React from 'react';
import * as Konva from 'react-konva';

export interface Props {
  color: string,
  rect: {
    X: number,
    Y: number,
    Width: number,
    Height: number
  },
  key: string
}

class BoardFieldComponent extends React.Component<Props, object>{
  constructor(props: Props){
    super(props);
  }

  shouldComponentUpdate(nextProps: Readonly<Props>, nextState: object){
    if (nextProps.color === this.props.color){
       return false;
     }
     return true;
  }

  render(){
    return (
      <Konva.Rect
        x={this.props.rect.X}
        y={this.props.rect.Y}
        width={this.props.rect.Width}
        height={this.props.rect.Height}
        fill={this.props.color}
        shadowBlur={this.props.color === 'white' ? 0 : 2}
        opacity={this.props.color === 'white' ? 0 : 0.5}
      />
    );
  }
}

export default BoardFieldComponent;


