import { Layer } from 'react-konva';
import * as React from 'react';

import { PlayerState, Point } from '../../utils/objectTypes';
import { layouter } from '../../utils/functions';
import PlayerComponent from './GameElements/PlayerVisual';
import TailComponent from './GameElements/TailVisual';

export interface Props {
  players: PlayerState,
  dimension: Point
}

function Players(props: Props = {dimension:{X:0, Y:0}, players:{names:[], byName:{}}}) {
    var playerState = props.players;
    var dimension = props.dimension;
    //temporarily hardcoded field size in pixels
    const canvasDimension: Point = {X:dimension.X*15,Y:dimension.Y*15};
    
    const playerComponents = playerState.names.map((curPlayer: string) => <PlayerComponent color={playerState.byName[curPlayer].color} rect={layouter(dimension, canvasDimension, playerState.byName[curPlayer].coords)} direction={playerState.byName[curPlayer].direction} speed={playerState.byName[curPlayer].speed} key={curPlayer}/>);

    const tailComponents = playerState.names.map((curPlayer: string) =>
    playerState.byName[curPlayer].tail.map((p: Point) => <TailComponent color={playerState.byName[curPlayer].color} rect={layouter(dimension, canvasDimension, p)}/>)
    );
    
    return (
      <Layer>      
      {playerComponents}
      {tailComponents}
      </Layer>
    );
}

export default Players;


